import React, { useEffect, useState } from "react";
import { Container, Form, Navbar, Button, ButtonGroup } from "react-bootstrap";
import { collection, doc, getDoc, setDoc } from "firebase/firestore";

import db from "./firebase";

const Clipboard = () => {
	const [text, setText] = useState("");
	const [status, setStatus] = useState("");
	const [isLoading, setIsLoading] = useState(true);
	const [lastLoaded, setLastLoaded] = useState("");

	const fetchData = async () => {
		setStatus("Loading...");

		const docRef = doc(collection(db, "clipboard"), "clipboard");
		const docSnap = await getDoc(docRef);
		const currentTime = new Date().toLocaleString();

		if (docSnap.exists()) {
			console.log("doc exists");
			setText(docSnap.data().text);
			setStatus(`Loaded - ${currentTime}`);
		} else {
			console.log("No such doc");
		}

		setIsLoading(false);
		// setStatus(`Loaded - ${lastLoaded}`);
	};

	useEffect(() => {
		fetchData();
	}, []);

	const handleChange = (e) => {
		if (!isLoading) {
			setText(e.target.value);
		}
	};

	const handleSave = async (textToSave) => {
		await setDoc(doc(collection(db, "clipboard"), "clipboard"), {
			text: text,
		});
		alert("Clipboard saved successfully");
	};

	const handleCopy = () => {
		navigator.clipboard
			.writeText(text)
			.then(() => alert("Text copied to clipboard"))
			.catch((err) => console.error("Error copying text: ", err));
	};

	return (
		<div>
			<Navbar expand="lg" className="bg-body-tertiary">
				<Container>
					<Navbar.Brand className="mx-auto">
						Online Clipboard
					</Navbar.Brand>
				</Container>
			</Navbar>

			<Form>
				<Container>
					<Form.Group
						className="mb-3 mt-4"
						controlId="exampleForm.ControlTextarea1"
					>
						<Form.Label className="text-xl">
							<h5>Clipboard Text</h5>
						</Form.Label>
						<Form.Control
							as="textarea"
							rows={20}
							value={text}
							onChange={handleChange}
							disabled={isLoading}
						/>
						<Form.Label className="mt-3 ">
							<h6>{isLoading ? "Loading..." : status}</h6>
						</Form.Label>

						<div>
							<Button
								variant="danger"
								onClick={handleSave}
								className="me-4"
								size="lg"
							>
								Save
							</Button>

							<Button
								variant="primary"
								onClick={handleCopy}
								size="lg"
							>
								Copy
							</Button>
						</div>
					</Form.Group>
				</Container>
			</Form>
		</div>
	);
};

export default Clipboard;
